@import './variables';

body {
    background-color: $background_color;
    font-family: $main_text;
    color: $main_text_color;
    font-size: 14px;
    font-weight: 700;
    line-height: normal;
}

p {
    margin: 0;
    padding: 0;
}

ul {
    list-style: none;
    margin-top: 0;
    margin-bottom: 0;
    padding-left: 0;
}

a {
    text-decoration: none;
}